.blogListItem {
    text-align: left;
    .title {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        font-size: 14px;
        font-weight: 600;
        color: var(--color);
    }
    .tagBox {
        display: inline-block;
        margin: 7px 0;
        .tag {
            margin-left: 1rem;
            padding: 0px 5px 1px;
            font-size: 0.6rem;
            font-weight: normal;
            color: var(--color);
            border: 1px solid var(--border_color);
            border-radius: 3px;
            &:first-child {
                margin-left: 0;
            }
        }
    }
    .author,
    .orderbyTime,
    .editType,
    .visits {
        display: inline-block;
        letter-spacing: 1px;
        margin-right: 8px;
        margin-bottom: 5px;
        font-size: 10px;
        color: var(--color);
    }
    .orderbyTime {
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .blogOperateBox {
        margin-top: 5px;
        .logOperateIcon {
            margin-right: 0.7rem;
            font-size: 1rem;
            color: var(--color);
            cursor: pointer;
            &.active {
                color: var(--color_active);
            }
            &:hover {
                color: var(--color_hover);
            }
        }
    }
}
